import './App.css';
import {Route, NavLink} from 'react-router-dom'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import Home from './Home/Home'
import Contacts from "./Contacts/Contact";
import Projects from "./Projects/Projects";
import Goodboy from "./Pages/Goodboy";
import House from "./Pages/House"
import Chel from "./Pages/Chel";
import Robot from "./Pages/Robot";
import './Home/Home.scss'
import './App.scss'
import logo from './assects/logo.png'
import Location from "./Pages/Location";
function App() {

  function arrTop(){
    if(window.pageYOffset > 600){
      document.querySelector('.arrowTop').style.opacity ='1'
    } else{
      document.querySelector('.arrowTop').style.opacity ='0'
    }
  }
  function scrollToTop(){
    scroll.scrollToTop({
      duration: 1000,
      smooth:'easeInOutQuart'
    });
    setTimeout(()=>{
      document.querySelector('.arrowTop').style.opacity ='0'
    },300)
  }
  return (
      <div className='wrapper' onWheel={arrTop.bind(this)}>
        <div className="App" >
          <div className='header'>
            <div className='headerWrapper'>
              <NavLink className='logo' to='/' exact>
                <img src={logo}/>
              </NavLink>
              {/*<div className='menuWrapper'>*/}
              {/*</div>*/}
            </div>
            <div className='menu'>
              <NavLink className='menuWord' to='/' exact>Главная</NavLink>
              <NavLink className='menuWord'  to='/projects'>Проекты</NavLink>
              <NavLink className='menuWord'  to='/contacts'>Контакты</NavLink>
            </div>
          </div>

          <div className='wrapper'>
            <Route path='/' exact>
              <Home/>
            </Route>
            <Route path='/contacts'>
              <Contacts/>
            </Route>
            <Route path='/projects'>
              <Projects/>
            </Route>
            <Route path='/good-boy'>
              <Goodboy/>
            </Route>
            <Route path='/mach-house'>
              <House/>
            </Route>
            <Route path='/raptor'>
              <Chel/>
            </Route>
            <Route path='/sci-fi-location'>
              <Location/>
            </Route>
            <Route path='/sci-fi-robot'>
              <Robot/>
            </Route>
          </div>

        </div>
        <div className='footer'>
            <div>© 2021</div>
        </div>
        {/*<svg className='arrowTop' onClick={scrollToTop.bind(this)} width="46" height="82" viewBox="0 0 46 82" fill="none" xmlns="http://www.w3.org/2000/svg">*/}
        {/*  <rect x="22" y="1" width="2" height="81" fill="black"/>*/}
        {/*  <path d="M22 1L23.4142 2.41421L1.49389 24.3345L0.0796749 22.9203L22 1Z" fill="black"/>*/}
        {/*  <path d="M22.5858 2.41455L24 1.00034L45.9203 22.9206L44.5061 24.3349L22.5858 2.41455Z" fill="black"/>*/}
        {/*  <path d="M23.0001 0L24.4143 1.41421L22.9142 2.91431L21.5 1.5001L23.0001 0Z" fill="black"/>*/}
        {/*</svg>*/}

        <svg className='arrowTop' onClick={scrollToTop.bind(this)} width="49" height="21" viewBox="0 0 49 21" fill="none" xmlns="http://www.w3.org/2000/svg">
          <g clip-path="url(#clip0)">
            <path d="M27.3818 4.71158L0.0244481 21L0 14.5993L24.5031 0L27.3818 4.71158Z" fill="#C6C6D0"/>
            <path d="M21.623 4.71158L48.9804 21L48.9987 14.5993L24.5018 0L21.623 4.71158Z" fill="#C6C6D0"/>
          </g>
          <defs>
            <clipPath id="clip0">
              <rect width="49" height="21" fill="white"/>
            </clipPath>
          </defs>
        </svg>

      </div>
  );
}

export default App;
