import React from 'react'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import mail from "../assects/mail.svg";
import photo1 from '../assects/location1.png'
import photo2 from '../assects/location2.png'
import photo3 from '../assects/location3.png'
import photo4 from '../assects/location4.png'
import photo5 from '../assects/location5.png'
import photo6 from '../assects/location6.png'
import photo7 from '../assects/location7.png'
import photo8 from '../assects/location8.png'
import photo9 from '../assects/location9.png'
import BeforeAfterSlider from 'react-before-after-slider'
import arrLeft from "../assects/arrowLeft.svg";
import arrRight from "../assects/arrowRight.svg";
import {NavLink} from "react-router-dom";
import ScrollAnimation from 'react-animate-on-scroll'
const Location = props => {

    const before = 'https://...example1.jpg'
    const after = 'https://...example2.jpg'

    return(
        <div className='project'>
            <div className='goodboy'>
                <div className='nameSection'>
                    <NavLink to='/raptor'><img src={arrLeft} className='arr'/></NavLink>
                    <div>Sci Fi Locationr</div>
                    <NavLink to='/mach-house'><img src={arrRight} className='arr'/></NavLink>
                </div>
            </div>
            <div className='textProject'>
                <div className='textTag'>
                    <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
                    <div>Эта локация создавалась в рамках прохождения курса по созданию 3D графики для компьютерных игр.</div>
                    </ScrollAnimation>
                </div>
                <div className='info'>
                    <ScrollAnimation animateIn="fadeInUp" delay={150}  animateOnce={true} duration={0.8}>
                    <div >Используемое ПО: 3Ds Max / 3DCoat / Unreal Engine</div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fadeInUp" delay={300}   animateOnce={true} duration={0.8}>
                    <div >Год реализации: 2019</div>
                    </ScrollAnimation>
                </div>
            </div>
            <ScrollAnimation animateIn="fadeInUp" delay={450}   animateOnce={true} duration={0.8}>
                <img className='ds' src={photo1}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" animateOnce={true} duration={0.8} className='space2'>
                <img className='ds' src={photo2}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8} className='space2 spaceBot'>
                <img className='ds' src={photo3}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
                <div className='smallBlocks spaceBot'>
                    <img className='ds' src={photo4}/>
                    <img className='ds' src={photo5}/>
                    <img className='ds' src={photo6}/>
                </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
                <div className='smallBlocks spaceBot'>
                    <img className='ds' src={photo7}/>
                    <img className='ds' src={photo8}/>
                    <img className='ds' src={photo9}/>
                </div>
            </ScrollAnimation>
        </div>
    )
}
export default Location