import React from 'react'
import './Home.scss'
import arrowDown from '../assects/arrowDown.svg'
import photo from '../assects/photo.jpg'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import ScrollAnimation from 'react-animate-on-scroll'
import "animate.css/animate.min.css";
const Home = props => {

    function scrollTo(){
        scroller.scrollTo('scroll-to-element', {
            duration: 1000,
            offset:-130,
            delay: 0,
            smooth: 'easeInOutQuart'
        })
        setTimeout(()=>{
            document.querySelector('.arrowTop').style.opacity ='1'
        },500)

    }

    return(
        <div className='wrapper'>
            <div className='hello'>
                <ScrollAnimation animateIn="fadeInUp"  animateOnce={true} duration={0.8}>
                    Привет, Я soofufa
                </ScrollAnimation>
            </div>
            <div className='helloText'>
                <ScrollAnimation animateIn="fadeInUp" delay={450}  animateOnce={true} duration={0.8}>
                    3D Artist
                </ScrollAnimation>
            </div>
            <img className='arrowDown' src={arrowDown} onClick={scrollTo.bind(this)}/>
                <div className='textWrapper' name='scroll-to-element'>
                    <ScrollAnimation animateIn="fadeInUp"  animateOnce={true} duration={0.8}>
                        <img src={photo}/>
                    </ScrollAnimation>

                    <div className='textBox'>
                        <ScrollAnimation animateIn="fadeInUp" delay={170} animateOnce={true} duration={0.8}>
                        <div className='nameSection1'>Обо мне</div>
                        <div className='textParagraph'>
                           <div>Меня зовут Соня, я из Санкт-Петербурга.</div>
                        </div>
                        <div className='textParagraph'>
                            Занимаюсь 3D-моделированием уже 2 года, создаю игровые локации, персонажей и другое. Также я могу создавать архитектурные визуализации.
                        </div>
                        <div className='textParagraph'>
                            Помимо трехмерного моделирования мне интересен графический дизайн и моушн-дизайн.
                        </div>
                        <div className='textParagraph'>
                            Я студентка Санкт-Петербургского государственного университета промышленных технологий и дизайна. Учусь на специальности прикладная информатика в дизайне.
                        </div>
                        </ScrollAnimation>
                    </div>

                </div>
        </div>

    )
}
export default Home