import React from 'react'
import './Contacts.scss'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import mail from '../assects/mail.svg'
import telegram from  '../assects/telegram.svg'
import inst from '../assects/inst.svg'
import artek from '../assects/artek.svg'
import ScrollAnimation from 'react-animate-on-scroll'
const Contacts = props => {
    return(
        <div className='contacts'>
            <ScrollAnimation animateIn="fadeInUp"  animateOnce={true} duration={0.8}>
            <div className='nameSection1'>Связаться со мной</div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"  delay={150} animateOnce={true} duration={0.8}>
            <div className='space'>
                <div className='textParagraph fl'>
                    <img className='imgIcon' src={mail}/>
                    <div>starabukhina@ya.ru</div>
                </div>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" delay={300}  animateOnce={true} duration={0.8}>
            <div className='space'>
                <div className='textParagraph fl'>
                   <a href='https://t.me/soofufa' target='_blank'><img className='imgIcon' src={telegram}/></a>
                    <div>@soofufa</div>
                </div>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" delay={450}  animateOnce={true} duration={0.8}>
            <div className='nameSection1'>Я в соцсетях</div>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" delay={600} animateOnce={true} duration={0.8}>
            <div className='space'>
                <div className='textParagraph fl'>
                    <a href='https://www.instagram.com/soofufa/' target='_blank'><img className='imgIcon' src={inst}/></a>
                   <a href='https://www.artstation.com/tarabsonya' target='_blank'><img className='imgIcon' src={artek}/></a>
                </div>
            </div>
            </ScrollAnimation>
        </div>
    )
}
export default Contacts