import React from 'react'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import mail from "../assects/mail.svg";
import photo1 from '../assects/sobaka1.png'
import photo2 from '../assects/sobaka2.png'
import photo3 from '../assects/sobaka3.png'
import BeforeAfterSlider from 'react-before-after-slider'
import arrLeft from "../assects/arrowLeft.svg";
import arrRight from "../assects/arrowRight.svg";
import {NavLink} from "react-router-dom";
import ScrollAnimation from 'react-animate-on-scroll'

const Goodboy = props => {

    const before = 'https://...example1.jpg'
    const after = 'https://...example2.jpg'

    return(
        <div className='project'>
        <div className='goodboy'>
            <div className='nameSection'>
                <NavLink to='/sci-fi-robot'><img src={arrLeft} className='arr'/></NavLink>
                <div>Good Boy</div>
                <NavLink to='/raptor'><img src={arrRight} className='arr'/></NavLink>
            </div>
        </div>
            <div className='textProject'>
                <div className='textTag'>
                    <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
                    <div>Модель этой собаки создавалась в рамках прохождения курса по 3D-моделированию</div>
                    </ScrollAnimation>
                </div>
                <div className='info'>
                    <ScrollAnimation animateIn="fadeInUp"  delay={150} animateOnce={true} duration={0.8}>
                    <div >Используемое ПО: 3Ds Max</div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fadeInUp"  delay={300} animateOnce={true} duration={0.8}>
                    <div >Год реализации: 2019</div>
                    </ScrollAnimation>
                </div>
            </div>
            <ScrollAnimation animateIn="fadeInUp"  delay={450} animateOnce={true} duration={0.8}>
            <BeforeAfterSlider
                before={photo2}
                after={photo1}
                width={1350}
                height={900}
            />
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp" animateOnce={true} duration={0.8}>
            <img className='space2 spaceBot' src={photo3}/>
            </ScrollAnimation>
        </div>
    )
}
export default Goodboy