import React from 'react'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import mail from "../assects/mail.svg";
import photo1 from '../assects/robot1.png'
import photo2 from '../assects/robot2.png'
import photo3 from '../assects/robot3.gif'
import BeforeAfterSlider from 'react-before-after-slider'
import arrLeft from "../assects/arrowLeft.svg";
import arrRight from "../assects/arrowRight.svg";
import {NavLink} from "react-router-dom";

const Robot = props => {

    const before = 'https://...example1.jpg'
    const after = 'https://...example2.jpg'

    return(
        <div className='project'>
            <div className='goodboy'>
                <div className='nameSection'>
                    <NavLink to='/mach-house'><img src={arrLeft} className='arr'/></NavLink>
                    <div>Sci Fi Robot</div>
                    <NavLink to='/good-boy'><img src={arrRight} className='arr'/></NavLink>
                </div>
            </div>
            <div className='textProject'>
                <div className='textTag'>
                    <div>Этот low-poly персонаж разработан для размещения на стоковых площадках.</div>
                </div>
                <div className='info'>
                    <div >Используемое ПО: 3Ds Max</div>
                    <div >Год реализации: 2020</div>
                </div>
            </div>
            <div className='smallBlocks'>
                <BeforeAfterSlider
                    before={photo2}
                    after={photo1}
                    width={650}
                    height={780}
                />
                <img className=' spaceBot gifImg' src={photo3}/>
            </div>


        </div>
    )
}
export default Robot