import React from 'react'
import AnimatedCard from "@sl-codeblaster/react-3d-animated-card";
import Card from 'react-animated-3d-card'
import {Route, NavLink} from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll'
function House(){
    return(
        <>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
        <NavLink className='house' to='/mach-house'>
            <Card
                style={{
                    background: "url('https://sun9-32.userapi.com/impg/z6n36LC4-U7xfgxm2Mu9C5EdhPxnYaQ-jmd5jw/8loctpIiZWs.jpg?size=450x451&quality=96&sign=d3b8baa295b6ac3df5001c838c8b3fb9&type=album')",
                    backgroundColor: "red",
                    width: '450px',
                    height: '450px',
                    cursor: 'pointer',
                    borderRadius: '0px',
                    shineStrength: 0.001
                }}
                onClick={() => console.log('Card clicked')}

            />

        </NavLink>


            <NavLink to='/sci-fi-robot'>
                <Card
                    style={{
                        background: "url('https://sun9-19.userapi.com/impg/9YEJPRM4BWkTXq3HQrwm_6sxs4otTlRv7KgnFg/vPGQ-M0yh6Q.jpg?size=450x450&quality=96&sign=c9709aed279b8f23794543bc273ff6c8&type=album')",
                        backgroundColor: "red",
                        width: '450px',
                        height: '450px',
                        cursor: 'pointer',
                        borderRadius: '0px',
                        shineStrength: 0.001
                    }}
                    onClick={() => console.log('Card clicked')}

                />
            </NavLink>



            <NavLink className='location' to='/sci-fi-location'>
                <Card
                    style={{
                        background: "url('https://sun9-75.userapi.com/impg/zbS4FEzfQSyV_kV1vKrMr-Y-PFjmwvXvf_56Ig/uvkbd2ZBm7s.jpg?size=450x450&quality=96&sign=bf1eff90efae49eb73eabc9efc20d81d&type=album')",
                        backgroundColor: "red",
                        width: '450px',
                        height: '450px',
                        cursor: 'pointer',
                        borderRadius: '0px',
                        shineStrength: 0.001
                    }}
                    onClick={() => console.log('Card clicked')}
                />
            </NavLink>


            <NavLink className='chel' to='/raptor'>
                <Card
                    style={{
                        background: "url('https://sun9-71.userapi.com/impg/W02wQpoz6d6N6wn0yyxkF76k2XJWQYUOg_xGhg/LiUB4ZTKcRQ.jpg?size=450x450&quality=96&sign=6b5c1820a15d975d67b2b221fc9905f6&type=album')",
                        backgroundColor: "red",
                        width: '450px',
                        height: '450px',
                        cursor: 'pointer',
                        borderRadius: '0px',
                        shineStrength: 0.001
                    }}
                    onClick={() => console.log('Card clicked')}
                />

            </NavLink>


            <NavLink className='sobaka' to='/good-boy'>
                <Card
                    style={{
                        background: "url('https://sun9-25.userapi.com/impg/3kBncoqUpKx6UKy1DhhpPPiXvskC_on0z7VM3w/Fd6cpj6UlxY.jpg?size=450x450&quality=96&sign=b40cefa122fbac09f6f533157066345c&type=album')",
                        backgroundColor: "red",
                        width: '450px',
                        height: '450px',
                        cursor: 'pointer',
                        borderRadius: '0px',
                        shineStrength: 0.001
                    }}
                    onClick={() => console.log('Card clicked')}
                />

            </NavLink>
            </ScrollAnimation>
            </>
    )
}
export default House