import React from 'react'
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import mail from "../assects/mail.svg";
import photo1 from '../assects/house1.png'
import photo2 from '../assects/house2.png'
import photo3 from '../assects/house3.png'
import photo4 from '../assects/house4.png'
import photo5 from '../assects/house5.png'
import photo6 from '../assects/house6.png'
import BeforeAfterSlider from 'react-before-after-slider'
import arrLeft from "../assects/arrowLeft.svg";
import arrRight from "../assects/arrowRight.svg";
import {NavLink} from "react-router-dom";
import ScrollAnimation from 'react-animate-on-scroll'

const House = props => {

    const before = 'https://...example1.jpg'
    const after = 'https://...example2.jpg'

    return(
        <div className='project'>
            <div className='machhouse'>
                <div className='nameSection'>
                    <NavLink to='/sci-fi-location'><img src={arrLeft} className='arr'/></NavLink>
                    <div>Mach House</div>
                    <NavLink to='/sci-fi-robot'><img src={arrRight} className='arr'/></NavLink>
                </div>
            </div>

            <div className='textProject'>
                <div className='textTag'>
                    <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
                    <div>Для изучения области визуализации архитектуры и интерьеров, я создала модель уже существующего дома, по готовым чертежам, найденным на просторах интернета.</div>
                    </ScrollAnimation>
                </div>
                <div className='info'>
                    <ScrollAnimation animateIn="fadeInUp" delay={150}   animateOnce={true} duration={0.8}>
                        <div >Используемое ПО: 3Ds Max</div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fadeInUp" delay={300}  animateOnce={true} duration={0.8}>
                        <div >Год реализации: 2020</div>
                    </ScrollAnimation>
                </div>
            </div>
            <ScrollAnimation animateIn="fadeInUp" delay={450}  animateOnce={true} duration={0.8}>
            <BeforeAfterSlider
                before={photo2}
                after={photo1}
                width={1350}
                height={900}
            />
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
            <img className='space2' src={photo3}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
            <img className='space2' src={photo4}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
            <img className='space2' src={photo5}/>
            </ScrollAnimation>
            <ScrollAnimation animateIn="fadeInUp"   animateOnce={true} duration={0.8}>
            <img className='space2 spaceBot' src={photo6}/>
            </ScrollAnimation>
        </div>
    )
}
export default House